import {createStore} from "vuex";
export default createStore({

    state: {
        userLevel: null,
      },
      mutations: {
        setUserLevel(state, level) {
          state.userLevel = level;
        },
      },
      actions: {
        // 필요하면 비동기 작업이 있을 경우 actions에 작성할 수 있습니다.
      },
      getters: {
        getUserLevel(state) {
          return state.userLevel;
        },
      },

});