import { createRouter, createWebHistory } from 'vue-router';


// import HomeView from '홈화면';

const routes = [
  // {
  //   path: '/',
  //   name: 'home',
  //   component: HomeView
  // },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },

  {
    path: '/login/:level',
    name: 'login',
    component: () => import('../components/comm/JH_Login.vue'),
  },
  

  {
    path: '/selectMemberType',
    name: 'selectMemberType',
    component: () => import('../components/comm/JH_SelectMemberType.vue')
  },

  // business
  {
    path: '/business/home',
    name: 'B_Home',
    component: () => import('../views/business/B_Home.vue')
  },
  {
    path: '/business/join',
    name: 'B_Join',
    component: () => import('../views/business/JH_B_Join.vue')
  },
  {
    path: '/business/updateInfo',
    name: 'B_updateInfo',
    component: () => import('../views/business/JH_B_UpdateInfo.vue')
  },


  // client
  {
    path: '/client/home',
    name: 'C_Home',
    component: () => import('../views/client/C_Home.vue')
  },
  {
    path: '/client/join',
    name: 'C_Join',
    component: () => import('../views/client/JH_C_Join.vue')
  },
  {
    path: '/client/myFriend',
    name: 'C_MyFriend',
    component: () => import('../views/client/JH_C_MyFriend.vue')
  },
  {
    path: '/client/mypage',
    name: 'C_Mypage',
    component: () => import('../views/client/JH_C_Mypage.vue')
  },
  {
    path: '/client/updateInfo',
    name: 'C_updateInfo',
    component: () => import('../views/client/JH_C_UpdateInfo.vue')
  },

]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;

